import React, { useState } from 'react';

import manutencao from '../../assets/manutencao.png';
import logoBottom from '../../assets/header.png';

const Manutencao = () => {
  const [email, setEmail] = useState('');
  const [message, setMessage] = useState('');
  const [messageType, setMessageType] = useState("");
  const [loading, setLoading] = useState(false);

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (email.trim() === "") {
      setMessage("Por favor, insira um e-mail válido.");
      setMessageType("error");
      return;
    }

    setLoading(true);

    try {
      const response = await fetch("https://api.nexgentech.com.br/api/salvar-email", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ email }),
      });

      setTimeout(async () => {
        setLoading(false);

        const result = await response.text();

        if (JSON.parse(result)["message"] === "cadastrado") {
          setMessage("Este e-mail já foi cadastrado em nosso sistema.");
          setMessageType("error");
          return;
        }

        if (response.ok) {
          setMessage("Obrigado! Enviaremos um e-mail quando o site estiver pronto.");
          setMessageType("success");
          setEmail("");
        } else {
          setMessage("Erro ao cadastrar e-mail.");
          setMessageType("error");
        }
      }, 1000);
    } catch (error) {
      console.log(error);
      setLoading(false);
      setMessage("Erro ao conectar ao servidor.");
      setMessageType("error");
    }
  };

  return (
<div className="flex flex-col justify-center items-center text-center min-h-screen bg-gradient-to-br from-[#0F1216] to-[#13181F] text-white px-6 pb-safe">
  
  {/* Imagem de manutenção */}
  <img src={manutencao} alt="Em Construção" className="w-64 sm:w-80 mb-6" />

  {/* Título e descrição */}
  <h1 className="text-3xl sm:text-4xl font-bold mb-4">
    Em Construção
  </h1>
  <p className="text-lg text-gray-400 max-w-md">
    Estamos trabalhando para oferecer a melhor experiência. Deixe seu e-mail e avisaremos quando estiver pronto!
  </p>

  {/* Formulário */}
  <form onSubmit={handleSubmit} className="flex flex-col sm:flex-row gap-3 mt-6">
    <input
      type="email"
      placeholder="Digite seu e-mail"
      value={email}
      onChange={(e) => setEmail(e.target.value)}
      className="px-4 py-2 text-black rounded-md w-72 sm:w-80 focus:outline-none focus:ring-2 focus:ring-[#A6F750]"
    />
    <button
      type="submit"
      className="flex items-center justify-center bg-[#A6F750] text-[#0F1216] px-4 py-2 rounded-md font-semibold transition-all hover:bg-[#C5F494] 
      w-full sm:w-32 text-center"
      disabled={loading}
    >
      {loading ? (
        <div className="w-5 h-5 border-2 border-white border-t-transparent rounded-full animate-spin"></div>
      ) : (
        "Avisar-me"
      )}
    </button>
  </form>

  {/* Mensagem de status */}
  {message && (
    <p className={`text-sm mt-2 ${messageType === "success" ? "text-[#A6F750]" : "text-[#FF4D4D]"}`}>
      {message}
    </p>
  )}

  {/* Logo e Rodapé (flexível) */}
  <div className="flex flex-col items-center flex-grow w-full">
    <img src={logoBottom} alt="Logo Nexgen Tech" className="w-auto sm:w-32 w-20 mt-10 mb-10" />
  </div>

  <div className="text-xs text-gray-500 pb-safe mb-5">
    © {new Date().getFullYear()} Nexgen Tech - Todos os direitos reservados.
  </div>
  
</div>


  );
};

export default Manutencao;
