import React, { useState } from "react";
import { BrowserRouter as Router, Route, Routes, useLocation } from "react-router-dom";
import { AnimatePresence, motion } from "framer-motion";
import Design from "./components/Design/Design";
import Header from "./components/Header/Header";
import MainContent from "./components/MainContent/MainContent";
import Footer from "./components/Footer/Footer";
import Manutencao from "./components/Manutencao/Manutencao";
import AssistVirtual from "./components/AssistVirtual/AssistVirtual";
import CookieBanner from "./components/Cookies/Cookies";
import Privacy from "./components/Privacy/Privacy";

function AnimatedRoutes() {
  const location = useLocation();

  return (
    <AnimatePresence mode="wait"> 
      <Routes location={location} key={location.pathname}>
        <Route path="/" element={<PageWrapper><MainContent /></PageWrapper>} />
        <Route path="/politica-de-privacidade" element={<PageWrapper><Privacy /></PageWrapper>} />
      </Routes>
    </AnimatePresence>
  );
}

const PageWrapper = ({ children }) => (
  <motion.div
    initial={{ opacity: 0, y: 20 }}
    animate={{ opacity: 1, y: 0 }}
    exit={{ opacity: 0, y: -20 }}
    transition={{ duration: 0.5 }}
  >
    {children}
  </motion.div>
);

function App() {
  const [activeMenu, setActiveMenu] = useState("Home");
  const emManutencao = true;

  return (
    <Router>
      {emManutencao ? (
        <Manutencao />
      ) : (
        <>
          <Design />
          <Header activeMenu={activeMenu} />
          <AssistVirtual />
          <AnimatedRoutes />
          <CookieBanner />
          <Footer />
        </>
      )}
    </Router>
  );
}

export default App;
